/* Add CSS custom properties for alpha colors if not defined */
:root {
  --color-primary-alpha: rgba(59, 130, 246, 0.1);
  --color-error: #dc2626;
}

[data-theme="dark"] {
  --color-primary-alpha: rgba(59, 130, 246, 0.2);
  --color-error: #ef4444;
}

/* Mobile progress card controls */
.mobile-progress__controls {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 0.5rem;
}

.mobile-progress__back {
  appearance: none;
  -webkit-appearance: none;
  border: 1px solid var(--color-border);
  background: var(--color-surface);
  color: var(--color-text);
  border-radius: var(--border-radius);
  padding: 0.5rem 0.75rem;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.mobile-progress__back:hover:not([disabled]) {
  border-color: var(--color-primary);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px var(--color-shadow);
}

.mobile-progress__back[disabled],
.mobile-progress__back[aria-disabled="true"] {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Hide stepper entirely on mobile */
@media (max-width: 768px) {
  .stepper { display: none; }
}

.stepper__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

/* Center the desktop stepper */
.stepper__header {
  justify-content: center;
  text-align: center;
}

.stepper__current-textline {
  font-weight: 700;
  color: var(--color-primary);
}

/* Center the desktop stepper */
.stepper__current-textline {
  text-align: center;
}

/* Step 3 layout improvements */
.step-panel[data-step="3"] .radio-group.booking-type {
  justify-content: center;
  gap: 1rem 1.5rem;
}

.step-panel[data-step="3"] .radio-group.booking-type label {
  min-width: 180px;
  justify-content: center;
}

.step-panel[data-step="3"] .radio-grid {
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
}

/* Checkbox card styling for first-time deep cleaning */
.checkbox-card {
  display: inline-flex;
  align-items: center;
  border: 2px solid var(--color-border);
  padding: 0.75rem 1rem;
  border-radius: var(--border-radius);
  background-color: var(--color-surface);
  cursor: pointer;
  gap: 0.6rem;
  font-weight: 600;
  transition: all var(--transition-fast);
  min-height: 48px;
  user-select: none;
}

.checkbox-card:hover {
  border-color: var(--color-primary);
  transform: translateY(-1px);
  box-shadow: 0 2px 8px var(--color-shadow);
}

.checkbox-card:has(input:checked) {
  background-color: var(--color-accent);
  color: var(--color-text);
  border-color: var(--color-primary);
  box-shadow: 0 2px 8px var(--color-shadow);
}

/* Mobile tweaks for Step 3 */
@media (max-width: 768px) {
  .step-panel[data-step="3"] .radio-group.booking-type label {
    min-width: 140px;
  }

  .step-panel[data-step="3"] .radio-grid {
    grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  }

  .checkbox-card {
    justify-content: center;
    width: 100%;
  }
}

.btn[disabled], .btn[aria-disabled="true"] {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Small-screen radio layout: make single-column radios stretch full width */
@media (max-width: 768px) {
  /* For non-grid radio groups, stack vertically and stretch */
  .radio-group:not(.radio-grid) {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
  }
  .radio-group:not(.radio-grid) label {
    width: 100%;
  }

  /* For grid radio groups, fill the grid cell width */
  .radio-grid label {
    width: 100%;
  }

  /* Booking type radios: ensure full width cards when stacked */
  .radio-group.booking-type label {
    width: 100%;
  }
}

/* Ensure tracks and fills render correctly */
.mobile-progress__bar,
.stepper__progress {
  position: relative;
}

.mobile-progress__fill {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  display: block;
}

.stepper__bar {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  display: block;
}